.overlay.showOverlay {
  opacity: 1;
  display: block;
}

.overlay {
  width: 342px;
  border: solid #e8e8e8 1px;
  padding: 14px;
  text-transform: uppercase;
  box-shadow: -5px 5px 5px #e8e8e8;
  opacity: 0;
  -webkit-transition: opacity 0.7s ease;
  transition: opacity 0.7s ease;
  display: none;
  z-index: 9999999;
}

.overlay h2,
.overlay .button {
  margin-bottom: 14px;
  text-align: center;
}

.overlay h2 a,
.overlay .button a {
  width: 100%;
}

.overlay .overlay__head {
  position: relative;
}

.overlay .overlay__head .overlay-exit::before {
  content: "";
  width: 20px;
  height: 2px;
  background: #2b2b2b;
  position: absolute;
  top: 10px;
  right: 0;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}

.overlay .overlay__head .overlay-exit::after {
  content: "";
  width: 20px;
  height: 2px;
  background: #2b2b2b;
  position: absolute;
  top: 10px;
  right: 0;
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}

.overlay .overlay__head .overlay-exit:hover {
  cursor: pointer;
}

.overlay .button {
  border: solid #cccccc 1px;
  max-width: 140px;
  margin: 10px auto;
  font-size: 12px;
  font-weight: bold;
  padding: 3px 0;
}

.overlay .button:hover {
  background-color: #cccccc;
  color: #ffffff;
  cursor: pointer;
}

/** MY ACCOUNT **/
.overlay-account ul {
  padding: 14px 0;
  border-bottom: solid #cccccc 1px;
  border-top: solid #cccccc 1px;
}

.overlay-account ul li {
  line-height: 30px;
  font-size: 15px;
}

.overlay-account ul li a::after {
  content: "";
  height: 8px;
  width: 8px;
  opacity: 0;
  position: absolute;
  background: #2b2b2b;
  border-radius: 50%;
}

.overlay-account ul li a:hover {
  cursor: pointer;
}

.overlay-account ul li a:hover::after {
  -webkit-transition: opacity 0.3s ease-in-out;
  transition: opacity 0.3s ease-in-out;
  opacity: 1;
}

.overlay-account::before {
  content: "";
  height: 8px;
  width: 10px;
  background: #ffffff;
  position: absolute;
  bottom: 100%;
  right: 25%;
  -webkit-clip-path: polygon(50% 0%, 0% 100%, 100% 100%);
          clip-path: polygon(50% 0%, 0% 100%, 100% 100%);
}

/** WISH LIST **/
.overlay-wish_list p {
  padding: 14px 0;
  border-bottom: solid #cccccc 1px;
  border-top: solid #cccccc 1px;
  font-size: 15px;
  text-transform: none;
}

.overlay-wish_list::before {
  content: "";
  height: 8px;
  width: 10px;
  background: #ffffff;
  position: absolute;
  bottom: 100%;
  right: 50px;
  -webkit-clip-path: polygon(50% 0%, 0% 100%, 100% 100%);
          clip-path: polygon(50% 0%, 0% 100%, 100% 100%);
}

/* SHOPPING BAG */
.overlay-shopping_bag p {
  padding: 14px 0;
  border-top: solid #cccccc 1px;
  font-size: 15px;
  text-transform: none;
}

.overlay-shopping_bag::before {
  content: "";
  height: 8px;
  width: 10px;
  background: #ffffff;
  position: absolute;
  bottom: 100%;
  right: 20px;
  -webkit-clip-path: polygon(50% 0%, 0% 100%, 100% 100%);
          clip-path: polygon(50% 0%, 0% 100%, 100% 100%);
}

